.aboutBtn {
    color: #ffffff !important;
    background-color: rgba(0, 179, 255, 0.565) !important;
    border-color: #009dff !important;
}

.FadeAway {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #ffffffff !important;
    background: linear-gradient(-90deg, rgba(255, 255, 255, 255) 60%, rgba(41, 41, 41, 1) 100%) !important;
    background: -moz-linear-gradient(-90deg, rgba(255, 255, 255, 0) 60%, rgba(41, 41, 41, 1) 100%) !important;
    background: -ms-linear-gradient(-90deg, rgba(255, 255, 255, 0) 60%, rgba(41, 41, 41, 1) 100%) !important;
    background: -o-linear-gradient(-90deg, rgba(255, 255, 255, 0) 60%, rgba(41, 41, 41, 1) 100%) !important;
    background: -webkit-linear-gradient(-90deg, rgba(255, 255, 255, 0) 60%, rgba(41, 41, 41, 1) 100%) !important;
}